body, html, #root{
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
  color: white;
  background-color: p;
}

.App{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.header{
  display: flex;
  width: 90%;
  height: 15%;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.logo-name{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo-name img{
  animation: blink 1s linear;
  margin-bottom: -35px;
}

@keyframes blink{
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
}

.logo-name p{
  animation: slide-down 1.5s  linear forwards;
}

@keyframes slide-down {
  0%{
    font-size: 10px;
    top: -50px;
  }
  50%{
    font-size: 15px;
    top: -25px
  }
  100%{
    font-size: 25px;
    top: 10px;
  }
}

.toggle-temp{
  font-weight: bold;
  border-radius: 8px;
  width: 10%;
  height: 30px;
  text-align: center;
  border: 2px solid black;
  background-color: black;
  color: white;
}



.Main{
  margin: 0;
  width: 100%;
  height: 60%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.location-5{
  margin: 0;
  display: flex;
  width: 40%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  gap: 10px;

}

.btn {
  width: 180px;
  background-color: transparent;
  padding: 3%;
  border: none;
  font-size: medium;
  color: white;
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  animation: slideIn 3s ease forwards;
  margin-right: 20%;;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.btn:hover{
  background-color: whitesmoke;
  color: black;
}

.location-5 h2{
  animation: BigSmall 2s ease forwards;
  margin-right: 23%;
}

@keyframes BigSmall{
  0%{
    transform: scale(5);
  }
  100%{
    transform: scale(1.5);
  }
}

.middle-panel{
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 10%;
}

.right-panel{
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.Footer{
  margin: 0;
  height: 20%;
  width: 100%;
  display: flex;
  flex-direction: row;
}
.search-section {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.search-input {
  padding: 5px;
  font-size: 16px;
  border: 1px solid #ddd;
  color: #ddd;
  border-radius: 5px;
  background-color: transparent;
}

.search-btn {
  padding: 5px 10px;
  font-size: 16px;
  background-color: transparent;
  color: grey;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer;
}

.search-btn:hover {
  background-color: grey;
  color: white;
  transform: scale(1.2);
}

.middle-panel .weather-info {
  margin-top: 15px;
  text-align: left;
}

.weather-info h1:hover{
  transform: scale(1.2);
}

.Footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position:relative;
  animation: fromdown 1s linear forwards;
  margin-top: 10px;
}

@keyframes fromdown {
  0%{
    bottom: -100px;
    opacity: 0;
  }
  50%{
    bottom: -50px;
    opacity: 1;
  }
  100%{
    bottom: 0px;
  }
}

.forecast-item {
  text-align: center;
  width: 15%;
  height: 100%;
  border: 3px solid black;
  border-radius: 5px;
}

.forecast-icon{
  width: 30%;
}

.right-panel h4,h3{
  color: grey;
}